* {
    box-sizing: border-box;
    margin-left: auto;
margin-right: auto;
text-align: center;

  }
  
 
  
  /* Float four columns side by side */
  .column {
    float: left;
    width: 25%;
    padding: 0 10px;
    margin-left: auto;
margin-right: auto;
text-align: center;


  }
  
  /* Remove extra left and right margins, due to padding */
  .row {margin: 0 -5px;
    align-items: center;
margin-left: 5cm;
text-align: center;
margin-right: auto;}
  
  /* Clear floats after the columns */
  .row:after {
      text-align: center;
    margin-left: 0cm;
    margin-right: auto;
    content: "";
    display: table;
    clear: both;


  }
  
  /* Responsive columns */
  @media screen and (max-width: 600px) {
    .column {
        margin-left: 15cm;
margin-right: auto;
      width: 100%;
      height:100%;
      display: block;
      margin-bottom: 20px;
          text-align: center;

    }
  }
  .card {

    margin-left: auto;
    margin-right: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 16px;
    text-align: center;
    grid-template-rows: max-content 200px 1fr;
    background-color: #f1f1f1;
  }  
  p{
    text-align: center;
    position: relative;
  bottom: 8px;
  left: 16px

  }
