body {
  margin-top: 1.5cm;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 18px;
  align-items:center;
  align-content: center;
  text-align: center;
  
}

button {
  padding: 16px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  align-items: center;
  text-align: center ;
  align-self: center;
  background-color: rgb(131, 21, 89);
  color: white;
  transition-duration: 0.4s;
  border-radius: 8px;
  border:none;
  cursor: pointer;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);

}

button:hover {
  background-color: rgb(235, 226, 226); /* Green */
  color:  rgb(131, 21, 89);
}
h1 {
  margin-top: 1cm;
  color: #f59504a4;;
  text-align: center;
  font-family: 'Tangerine', serif;
  font-size: 48px;
  border-color: rgb(153, 147, 202);
}
p,h3, h4 {
  color:rgb(131, 21, 89);
  margin-left: 2cm;
  margin-right: 2cm;
  margin-bottom: 0;
  
}
label{
  color:rgb(131, 21, 89);

}
.instruction{
  color:#8b8787f3;
  text-align: center;
  

}
.inst{
  color:rgb(83, 20, 59);
  margin-left: 2cm;
  margin-right: 2cm;
  margin-bottom: 0;
  text-align: center;

}
.container {
  position: relative;
  text-align: center;
  color: white;
}

.centered {
  position: absolute;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.bottom-left {
  position: absolute;
  bottom: 8px;
  left: 16px;
}
.photo{
  margin-top: 2cm;
 position: relative;
  margin-right:auto;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  opacity: 0.5;
  background-size: contain;
  width:700px;
  height: 600px;
}
.photo1{
  margin-top: 2cm;
 position: relative;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  background-size: contain;
  width:100%;
  
}

nav {
  text-align: center;
  position: fixed;
  top: 0;
  width: 100%;
  background:#f3edeade;
}
.ing{
  color:#f59504a4;
  text-align: center;
  text-decoration-line: underline;
  text-decoration-color: purple;

}

.ingredients{
  
  display: inline-block;
  width: 600px;
  color:white;
  height: 450px;
  background: #e6ddddd5;
  border: 1px solid rgba(74, 74, 74, 0.075);
  margin-top: 2cm;
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(245, 191, 15, 0.19);
  border-radius: 2%;
  text-shadow: -0.5px 0 purple, 0 0.1px purple, 0.1px 0 purple 0 -0.1px purple;

}

nav ul {
  
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav li {
  
  display: inline-block;
  padding-top: 16px;
  padding-bottom: 16px;
}

nav a {
  font-family: 'Georgia', serif;
  text-decoration: none;
  color:rgb(201, 8, 8);
  font-weight: bold;
  padding: 16px 32px;
}

nav a:hover {

  background:#fae4e19d;
  color: rgb(158, 110, 110);
}

#page-body {
  max-width: 1500px;
  margin: auto;
  padding-top: 64px;

}

.article-list-item {
  margin-left: 2cm;
  margin-right: 2cm;
  text-decoration: none;
  color: black;
  padding: 16px 0 0 0;
}

.article-list-item p {
  margin-left: 2cm;
  margin-right: 2cm;
  padding-bottom: 32px;
  border-bottom: 2px solid rgba(124, 2, 2, 0.753);

}

#add-comment-form {
  padding: 16px;
  border-bottom:2px solid black;
}

#add-comment-form h3 {
 margin-bottom: ;
}

#add-comment-form input, textarea {
  display:block;
  border:2px solid #f59504a4;
  width: 300px;
  padding: 8px;
  border-radius: 8px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
align-items: center;
text-align: center;
  font-size: 18px;
  margin-bottom: 16px;
  margin-left: 7cm;
}

#add-comment-form button {
  width: 320px;
}

#upvotes-section button, p {
  display: inline-block;
  margin-right: 16px;
}


.comment {
  border-bottom: 2px solid rgba(124, 2, 2, 0.753);
  padding: 16px;
}